<template>
  <div id="app" class="dash container">
    <br /><br />
    <h1>Sign In</h1>

    <form @submit.prevent="signup">
      <div class="form-group">
        <input @input="$v.email.$touch()" class="form-control" v-model="email" type="text" placeholder="email" name="email" />
        <small v-if="!$v.email.email" id="emailHelp" class="form-text text-muted">
          Please provide a valid email
        </small>
        <small v-if="!$v.email.required" id="emailHelp" class="form-text text-muted">
          This field must not be empty
        </small>
      </div>
      <p class="errorMsg" v-if="notFound===true">Email not found. Register first.</p>
      <div class="form-group">
        <input class="form-control" v-model="password" type="password" placeholder="password" name="password" @input="$v.password.$touch()" />
        <small id="emailHelp" v-if="!$v.password.required" class="form-text text-muted">This field must not be empty</small>
        <small id="emailHelp" v-if="!$v.password.minLength" class="form-text text-muted">password must contain min 6
          characters</small>
      </div>
      <p class="errorMsg" v-if="passErr===true">Email or Password is incorrect</p>
      <button type="submit" class="btn btn-success btn-block" :disabled="$v.$invalid">Sign In</button>
    </form>
  </div>
</template>

<script>
  import axios from 'axios';
  import router from '../router'
  import jwt_decode from 'jwt-decode'
  import {
    required,
    email,
    minLength
  } from 'vuelidate/lib/validators'
import { mapMutations } from 'vuex';

  export default {
    name: 'SignInForm',
    data() {
      return {
        email: '',
        password: '',
        passErr: undefined,
        notFound: undefined
      }
    },
     computed: {
      ...mapMutations([
         'setSign'
      ]),
    },
    validations: {
      email: {
        required,
        email
      },
      password: {
        required,
        minLength: minLength(6)
      }
    },
    mounted() {
      this.$store.commit('setCurrentPage', 'signin');
    },
    methods: {
      signup: function (e) {
        axios({
            method: 'post',
            url: '/api/user/signin',
            data: {
              email: this.email,
              password: this.password
            }
          }).then(response => {
            console.log(response);
            router.push({
              path: '/'
            });
            const {
              token
            } = response.data;
            localStorage.setItem('token', token);
            this.$store.commit('setSign');
          })
          .catch(error => {
            if (error.response.status === 400) {
              this.passErr = true;
            } else if (error.response.status === 404) {
              this.notFound = true;
            }
            console.log(error);
          });
      }
    }
  }

</script>
<style scoped>
  .dash {
    width: 50vw;
     margin-top: 60px;
  }

  @media screen and (max-width: 950px) {
    .dash {
      width: 100vw;
    }
  }

  .errorMsg {
    color: red
  }

</style>



