<template>
  <div class="dash container">
    <br />
    <h1>Create a new Blog</h1>
    <form @submit.prevent="submit">
      <div class="form-group ">
        <label for="exampleFormControlInput1">Title</label>
        <input type="text" class="form-control" id="exampleFormControlInput1" v-model="title" placeholder="title">
      </div>
      <div class="form-group">
        <label for="exampleFormControlSelect1">Category</label>
        <select v-model="category" class="form-control" id="exampleFormControlSelect1">
          <option disabled value="">Please select one</option>
          <option value="technology">Technology</option>
          <option value="relationship">Relationship</option>
          <option value="travel">Travel</option>
          <option value="books">Books</option>
          <option value="fitness">Fitness</option>
        </select>
      </div>
      <div class="form-group">
        <label for="exampleFormControlTextarea1">Content</label>
        <textarea v-model="content" class="form-control" id="exampleFormControlTextarea1" rows="3"></textarea>
      </div>
      <button type="submit" class="btn btn-success btn-block">Create</button>
    </form>
  </div>
</template>

<script>
  import axios from 'axios'
  import router from '../router'
  export default {
    name: 'AddBlogForm',
    data() {
      return {
        category: '',
        title: '',
        content: ''
      }
    },
    methods: {
      submit: function (e) {
        let config = {
          headers: {
            Token: localStorage.getItem('token'),
          }
        };
        let data = {
          title: this.title,
          category: this.category,
          content: this.content
        }
        axios.post('http://localhost:4000/api/blog/create', data, config)
        .then(response => {
            console.log(response);
            router.push({
              path: '/'
            });
          })
          .catch(error => {
            console.log(error);
          });
      }
    }
  }

</script>

<style scoped>
  .dash {
    width: 50vw;
    margin-top: 60px;
  }

  @media screen and (max-width: 950px) {
    .dash {
      width: 100vw;
    }
  }

</style>



