<template>
  <div class="dash container">
    <br />
    <router-link to='/addblog'><button type="button" v-if='getSign' class="btn btn-success btn-block">Add a new Blog</button></router-link>
    <br />
    <div v-for="(blog, index) in getBlogs" v-bind:key="index" @click="onBlogClick(blog)">
      <div class="card">
        <div class="card-body">
          <h5 class="card-title">{{blog.title}}</h5>
          <p class="card-text">{{blog.content.substring(0, 100)}}...</p>
        </div>
      </div>
      <br />
    </div>
  </div>

</template>

<script>
  import axios from 'axios'
  import router from '../router'
  import {
    mapGetters,
    mapActions
  } from 'vuex'
  export default {
    name: 'Dashboard',
    data() {
      return {
        addobj: ''
      }
    },
    computed: {
      ...mapGetters([
        'getBlogs', 'getSign'
      ])
    },
    methods: {
      onBlogClick: function (blog) {
        this.$store.commit('setCurrentBlog', blog)
        router.push({
          path: `/blog/${blog._id}`
        });
      }
    },
    mounted() {
      this.$store.commit('setSign');
      this.$store.commit('setCurrentPage', 'dashboard');
      axios({
          method: 'get',
          url: '/api/blog',
        }).then(res => {
          const data = res.data
          this.$store.commit('setBlogs', res.data)
        })
        .catch(error => {
          console.log(error);
          return '';
        })
    }
  }

</script>

<style scoped>
  .dash {
    width: 50vw;
     margin-top: 60px;
  }

  .card {
    cursor: pointer;
  }

  .card:hover {
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    transition: .2s;
  }

  @media screen and (max-width: 950px) {
    .dash {
      width: 100vw;
    }

  }

</style>



