import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);
export const store = new Vuex.Store({
  state: {
    blogs: [],
    user: {},
    pageOn: undefined,
    currentBlog: {
      date: '',
      content: '',
      title: '',
      author: '',
      category: ''
    },
    isSign:false,
  },
  getters: {
    getBlogs: state => {
      return state.blogs;
    },
    getUser: state => {
      return state.user;
    },
    getPage: state => {
      return state.pageOn;
    },
    getCurrentBlog: state => {
      return state.currentBlog;
    },
    getSign: state => {
      return state.isSign;
    }
  },
  mutations: {
    setUser(state, user){
      state.user=user;
    },
    setBlogs(state, blogs) {
      state.blogs = blogs
    },
    setCurrentBlog(state, blog) {
      state.currentBlog.title = blog.title;
      state.currentBlog.date = blog.date;
      state.currentBlog.content = blog.content;
      state.currentBlog.author = blog.author;
      state.currentBlog.category = blog.category;
    },
    setCurrentPage(state, page) {
      state.pageOn = page;
    },
    setSign(state) {
      state.isSign = !!localStorage.getItem('token');
    }
  }
});



