<template>
<div class="blog container">
  <h5>/{{getCurrentBlog.category}}</h5>
  <h1>{{getCurrentBlog.title}}</h1>
  <h6>{{getCurrentBlog.author}}   -   {{getCurrentBlog.date}}</h6><br/>
  <p>{{getCurrentBlog.content}}</p>
  </div>
</template>

<script>
  import axios from 'axios'
  import {
    mapGetters
  } from 'vuex'
  export default {
    name: 'Dashboard',
    data(){
      return{
        blog:undefined,
        blogId:''
      }
    },
    computed: {
      ...mapGetters([
        'getCurrentBlog'
      ]),
    },
    mounted() {
      this.$store.commit('setCurrentPage','dashboard')
      
    }
  }

</script>

<style scoped>

.blog{
    padding: 10px 20px;
    margin-top: 60px;
}
.blog{
  width: 70vw;
}
@media screen and (max-width: 950px){
  .blog{
    width: 100vw;
  }
}
</style>



