<template>
 <nav class="navbar navbar-expand-lg navbar-light bg-light">
  <router-link to='/'><a class="navbar-brand mb-0 h1">Wisdom Blogs</a></router-link>

  <router-link to='/signin'><button v-if='getPage==="dashboard"&&getSign===false'
   type="button" class="btn btn-success">Sign In</button></router-link>
  
  <router-link to='/signin'><button v-if='getPage==="signup"' type="button"
   class="btn btn-success">Sign In</button></router-link>

 <router-link to='/profile/abc'><button v-if='getPage==="dashboard"&&getSign===true'
  type="button" class="btn btn-success" >Profile</button></router-link>

  <router-link to='/signup'><button v-if='getPage==="signin"' 
  type="button" class="btn btn-success">Sign Up</button></router-link>

  <router-link to='/'><button v-if='getPage==="profile"' 
  type="button" class="btn btn-success" @click='logOut'>Sign Out</button></router-link>
    

 <router-link to='/credits'><i id="icon" class="fas fa-info-circle"></i></router-link>

</nav>
</template>

<script>
 import {
    mapGetters
  } from 'vuex'
  import router from '../router'

export default {
    name: 'Navbar',
    computed: {
      ...mapGetters([
        'getPage','getSign'
      ])
    },
    methods:{
      logOut:function(){
        localStorage.removeItem("token");
      }
    }
}

</script>

<style scoped>
button{
    position: fixed;
    right: 60px;
    top: 8px;
}
.navbar{
  z-index: 100;
  position: fixed;
  width: 100vw;
  top: 0;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}
i{
  font-size: 35px;
}
#icon{
  position: fixed;
  right: 10px;
  top: 9px;
}

</style>



