<template>
  <div id="app" class="dash container">
      <br/><br/>
    <h1>Sign Up</h1>
    <form @submit.prevent="signup">
      <div class="form-group">
        <input class="form-control" v-model="name" type="text" placeholder="name"
         name="name" @input="$v.name.$touch()"/>
         <small v-if="!$v.name.required" id="emailHelp" class="form-text text-muted">
            This field must not be empty
        </small>
        <small v-if="!$v.name.minLength" id="emailHelp" class="form-text text-muted">
            Name must contain 4 characters
        </small>
      </div>
      <div class="form-group">
        <input class="form-control" v-model="email" type="email" placeholder="email" 
        name="email" @input="$v.email.$touch()" />
        <small v-if="!$v.email.email" id="emailHelp" class="form-text text-muted">
            Please provide a valid email
        </small>
        <small v-if="!$v.email.required" id="emailHelp" class="form-text text-muted">
            This field must not be empty
        </small>
      </div>
      <p class="errorMsg" v-if="found===true">Email already exists.</p>
      <div class="form-group">
        <input class="form-control" v-model="password" type="password"
         placeholder="password" name="password" @input="$v.password.$touch()" />
         <small id="emailHelp" v-if="!$v.password.required" class="form-text text-muted">This field must not be empty</small>
        <small id="emailHelp" v-if="!$v.password.minLength" class="form-text text-muted">password must contain min 6 characters</small>
      </div>
      <button type="submit" class="btn btn-success btn-block" :disabled="$v.$invalid">Sign Up</button>
    </form>
  </div>
</template>

<script>
  import axios from 'axios';
  import router from '../router'
  import {required, email, minLength} from 'vuelidate/lib/validators'
  export default {
    name: 'SignUpForm',
    data() {
      return {
        name: '',
        email: '',
        password: '',
        errorCode:false,
        found:false
      }
    },
    mounted() {
      this.$store.commit('setCurrentPage', 'signup');
    },
    validations:{
        name:{
            required, minLength:minLength(4)
        },
        email:{
            required, email
        },
        password:{
            required, minLength:minLength(6)
        }
    },
    methods: {
      signup: function (e) {
          if(true){
        axios({
            method: 'post',
            url: '/api/user/signup',
            data: {
              name: this.name,
              email: this.email,
              password: this.password
            }
          }).then(response=> {
            console.log(response);
            router.push({
              path: '/signin'
            });
          })
          .catch(error=> {
            if(error.response.status===400){
              this.found=true;
            }
            console.log(error);
          });
      }
    }}
  }

</script>
<style scoped>
.dash{
  width: 50vw;
   margin-top: 60px;
}
@media screen and (max-width: 950px){
  .dash{
    width: 100vw;
  }
}
.errorMsg{
  color:red
}
</style>


